import type { NextPage } from 'next';
import { terms } from '../data/terms';
import { useState } from 'react';
import { HeroHeader } from '@/components/HeroHeader';
import "../styles/globals.css";

const Home: NextPage = () => {
  const [expandedTerm, setExpandedTerm] = useState<number | null>(null);

  const toggleTerm = (termId: number) => {
    setExpandedTerm(expandedTerm === termId ? null : termId);
  };

  return (
    <div>
      {/* Hero Section */}
      <Hero/>
      {/* <div className="hero-bg h-96 flex items-center justify-center">
        <h1 className="text-6xl font-bold text-white bg-black bg-opacity-50 p-4 rounded-lg">
          SoloArtJourney
        </h1>
      </div> */}

      {/* Grid of Cards */}
      <div className="container mx-auto p-6">
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
          {terms.map((term) => (
            <div
              key={term.id}
              className="border p-6 rounded-lg shadow-lg hover:shadow-xl transition-shadow cursor-pointer"
              onClick={() => toggleTerm(term.id)}
            >
              <h2 className="text-2xl font-semibold mb-4">{term.title}</h2>
              {term.locked ? (
                <a
                  href={term.affiliateLink}
                  target="_blank"
                  rel="noopener noreferrer"
                  className="text-blue-500 hover:underline"
                >
                  Unlock Term {term.id} on Gumroad
                </a>
              ) : (
                expandedTerm === term.id && (
                  <div>
                    {term.topics?.map((topic) => (
                      <div key={topic.id} className="mb-4">
                        <h3 className="text-xl font-medium">{topic.title}</h3>
                        <ul className="list-disc list-inside">
                          {topic.resources.map((resource, index) => (
                            <li key={index} className="hover:text-blue-500">
                              <a href="#" onClick={(e) => e.stopPropagation()}>
                                {resource}
                              </a>
                            </li>
                          ))}
                        </ul>
                      </div>
                    ))}
                  </div>
                )
              )}
            </div>
          ))}
        </div>
      </div>
    </div>
  );
};

export default Home;